<template>
  <div class="login">
    <h2>Login</h2>
    <form @submit.prevent="login">
      <label for="username">Username</label>
      <input type="text" id="username" v-model="state.username" />
      <label for="password">Password</label>
      <input type="password" id="password" v-model="state.password" />
      <button type="submit">Login</button>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import { reactive, inject } from "vue";
import { ElMessage } from "element-plus"; // 导入消息框组件
import router from "@/router"; // 导入Vue Router实例

export default {
  setup() {
    const state = reactive({
      username: "",
      password: "",
      isLoggedIn: false,
    });

    const setIsLoggedIn = inject("setIsLoggedIn");

    const login = async () => {
      try {
        const response = await axios.post("/api/user/login", {
          username: state.username,
          password: state.password,
        });
        if (response.data.msg !== "login success") {
          throw new Error(response.data.message);
        }
        setIsLoggedIn(true);
        ElMessage.success("登录成功"); // 弹出消息框
        setTimeout(() => {
          router.push("/"); // 2秒后重定向到首页
        }, 2000);
      } catch (error) {
        ElMessage.error("登录失败");
      }
    };

    return {
      state,
      login,
    };
  },
};
</script>

<style>
.login {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
}

.login h2 {
  margin-top: 0;
}

.login form {
  display: flex;
  flex-direction: column;
}

.login label {
  margin-bottom: 5px;
}

.login input {
  padding: 5px;
  margin-bottom: 10px;
}

.login button {
  padding: 5px 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.login button:hover {
  background-color: #0069d9;
}
</style>
