<template>
  <div class="challenges">
    <div class="menu">
      <h3>Categories</h3>
      <ul>
        <li @click="selectedCategory.value = false">ALL</li>
        <li
          v-for="category in categories"
          :key="category"
          @click="selectedCategory.value = category"
        >
          {{ category }}
        </li>
      </ul>
      <div class="flag-submit">
        <input type="text" placeholder="Enter flag" v-model="flag.value" />
        <button @click="submitFlag">Submit</button>
      </div>
    </div>
    <div class="challenge-list">
      <div
        v-for="challenge in filteredChallenges"
        :key="challenge.id"
        class="challenge"
        v-bind:style="{ backgroundColor: categoryColors[challenge.category] }"
        @click="showChallengeDetails(challenge)"
      >
        <p>{{ challenge.title }}</p>
      </div>
      <div
        v-if="selectedChallenge.value"
        class="challenge-details"
        @click="selectedChallenge.value = false"
      >
        <h4>{{ selectedChallenge.value.title }}</h4>
        <p>{{ selectedChallenge.value.content }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { reactive, onMounted, computed, inject } from "vue";
import { ElMessage } from "element-plus"; // 导入消息框组件
import router from "@/router"; // 导入Vue Router实例

export default {
  setup() {
    const state = reactive({
      data: [],
    });

    const flag = reactive({
      value: "",
    });

    const submitFlag = async () => {
      try {
        const response = await axios.post("/api/challenge/submit", {
          flag: flag.value,
        });
        if (response.data.msg !== "success") {
          throw new Error(response.data.message);
        }
        ElMessage.success("Flag正确");
      } catch (error) {
        ElMessage.error("Flag错误");
      }
    };

    const selectedCategory = reactive({
      value: "",
    });

    const selectedChallenge = reactive({
      value: "",
    });

    const categoryColors = {
      web: "#ffa07a",
      pwn: "#87cefa",
      rev: "#90ee90",
      crypto: "#f08080",
      misc: "#dda0dd",
    };

    const setIsLoggedIn = inject("setIsLoggedIn");

    const fetChallenges = async () => {
      try {
        const response = await axios.get("/api/challenge/list");
        if (response.data.msg !== "success") {
          throw new Error(response.data.message);
        }
        state.data = response.data.data;
        setIsLoggedIn(true);
      } catch (error) {
        ElMessage.error("请先登陆");
        setTimeout(() => {
          router.push("/login"); // 2秒后重定向到首页
        }, 2000);
      }
    };

    onMounted(fetChallenges);

    const categories = computed(() => {
      const set = new Set();
      state.data.forEach((challenge) => {
        set.add(challenge.category);
      });
      return Array.from(set);
    });

    const filteredChallenges = computed(() => {
      if (selectedCategory.value) {
        return state.data.filter(
          (challenge) => challenge.category === selectedCategory.value
        );
      } else {
        return state.data;
      }
    });

    const showChallengeDetails = (id) => {
      const challenge = state.data.find((c) => c === id);
      selectedChallenge.value = challenge;
    };

    return {
      state,
      categories,
      categoryColors,
      filteredChallenges,
      selectedCategory,
      selectedChallenge,
      showChallengeDetails,
      flag,
      submitFlag,
    };
  },
};
</script>

