<template>
  <div class="profile">
    <h2>Profile</h2>
    <p>Username: {{ state.username }}</p>
    <p>Email: {{ state.email }}</p>
  </div>
</template>

<script>
import axios from "axios";
import { reactive, onMounted, inject } from "vue";
import { ElMessage } from "element-plus"; // 导入消息框组件
import router from "@/router"; // 导入Vue Router实例

export default {
  setup() {
    const state = reactive({
      username: "",
      email: "",
    });

    const setIsLoggedIn = inject("setIsLoggedIn");

    const fetchProfile = async () => {
      try {
        const response = await axios.get("/api/user/profile");
        if (response.data.msg !== "success") {
          throw new Error(response.data.message);
        }
        state.username = response.data.user.username;
        state.email = response.data.user.email;
        setIsLoggedIn(true);
      } catch (error) {
        ElMessage.error("请先登陆");
        setTimeout(() => {
          router.push("/login"); // 2秒后重定向到首页
        }, 2000);
      }
    };

    onMounted(fetchProfile);

    return {
      state,
    };
  },
};
</script>

<style>
.profile {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
}

.profile h2 {
  margin-top: 0;
}

.profile form {
  display: flex;
  flex-direction: column;
}

.profile label {
  margin-bottom: 5px;
}

.profile input {
  padding: 5px;
  margin-bottom: 10px;
}

.profile button {
  padding: 5px 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.profile button:hover {
  background-color: #0069d9;
}
</style>
