<template>
  <nav>
    <router-link to="/">Home | </router-link>
    <router-link to="/about">About | </router-link>
    <router-link to="/challenge">Challenge | </router-link>
    <router-link to="/profile">Profile | </router-link>
    <router-link v-if="!state.isLoggedIn" to="/login">Login | </router-link>
    <router-link v-if="!state.isLoggedIn" to="/register">Register </router-link>
    <router-link v-if="state.isLoggedIn" to="/logout">Logout</router-link>
  </nav>
  <router-view />
</template>
<script>
import { reactive, provide } from "vue";

export default {
  setup() {
    const state = reactive({
      isLoggedIn: false,
    });

    const setIsLoggedIn = (value) => {
      state.isLoggedIn = value;
      // console.log(state.isLoggedIn);
    };

    provide("setIsLoggedIn", setIsLoggedIn);

    return {
      state,
    };
  },
};
</script>
