<template>
  <div class="profile">
    <h2>Logout</h2>
    <p>Goodbye</p>
  </div>
</template>

<script>
import axios from "axios";
import { onMounted, inject } from "vue";
import { ElMessage } from "element-plus"; // 导入消息框组件
import router from "@/router"; // 导入Vue Router实例

export default {
  setup() {
    const setIsLoggedIn = inject("setIsLoggedIn");

    onMounted(() => {
      axios.post("/api/user/logout");
      setIsLoggedIn(false);
      ElMessage.success("注销成功");
      setTimeout(() => {
        router.push("/login"); // 2秒后重定向到首页
      }, 2000);
    });

    return {};
  },
};
</script>

