<template>
  <div class="register">
    <h2>Register</h2>
    <form @submit.prevent="register">
      <label for="username">Username</label>
      <input type="text" id="username" v-model="state.username" />
      <label for="password">Password</label>
      <input type="password" id="password" v-model="state.password" />
      <label for="email">Email</label>
      <input type="email" id="email" v-model="state.email" />
      <button type="submit">Register</button>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import { reactive } from "vue";
import { ElMessage } from "element-plus"; // 导入消息框组件
import router from "@/router"; // 导入Vue Router实例

export default {
  setup() {
    const state = reactive({
      username: "",
      password: "",
      email: "",
    });

    const register = async () => {
      try {
        const response = await axios.post("/api/user/register", {
          username: state.username,
          password: state.password,
          email: state.email,
        });
        if (response.data.msg !== "register success") {
          throw new Error(response.data.message);
        }
        ElMessage.success("注册成功");
        setTimeout(() => {
          router.push("/login"); // 2秒后重定向到首页
        }, 2000);
      } catch (error) {
        ElMessage.error("注册失败");
      }
    };

    return {
      state,
      register,
    };
  },
};
</script>

<style>
.register {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
}

.register h2 {
  margin-top: 0;
}

.register form {
  display: flex;
  flex-direction: column;
}

.register label {
  margin-bottom: 5px;
}

.register input {
  padding: 5px;
  margin-bottom: 10px;
}

.register button {
  padding: 5px 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.register button:hover {
  background-color: #0069d9;
}
</style>
